$(function () {
    ajaxService.bannerList({pageSize: 10}, function (a) {
        if (0 == a.code) {
            let i = "";
            for (let e = 0; e < a.data.length; e++) {
                var t = a.data[e];
                i += `<a href="${t.link_to || "javascript:;"}" target="${t.link_to ? "_blank" : ""}" class="${t.link_to ? "item swiper-slide" : "swiper-slide item default"}">
           <span class="img" data-url="${t.image}" style="background: url('${t.image}') no-repeat center;"></span>
           </a>`
            }
            $(".swiper-wrapper").html(i), 8 == tool.myBrowser() && $(".swiper-wrapper .item").each(function () {
                var e = $(this).children(".img").data("url");
                $(this).children(".img").css("filter", `progid:DXImageTransform.Microsoft.AlphaImageLoader(src='${e}',sizingMethod='scale')`)
            });
            var e = new Swiper(".swiper-container", {
                autoplay: !1,
                loop: !0,
                calculateHeight: !0,
                autoplayDisableOnInteraction: !1,
                pagination: ".pagination",
                paginationClickable: !0,
                swipeToNext: !1,
                swipeToPrev: !1
            });
            $(".swiper-container .prev").click(function () {
                e.swipePrev()
            }), $(".swiper-container .next").click(function () {
                e.swipeNext()
            })
        }
    }), ajaxService.indexNews({num: 4}, function (t) {
        if (0 == t.code) {
            let a = "";
            for (let i = 0; i < t.data.length; i++) {
                const r = t.data[i];
                let e = r.brief;
                e && 110 < e.length && (e = e.slice(0, 110) + "...");
                var n = r.update_at && r.update_at.split(" ")[0].substr(5, 5);
                a += `<a class="swiper-slide news-item ${i % 2 == 0 ? "fl" : "fr"}" target="_blank" href="/en/about_newsdetail.html?id=${r.id}">
          <div class="news-item-img"><image src="${r.image}" /></div>
          <div class="news-item-text"><h4 title="${r.title}">${r.title}</h4><p class="news-item-brief">${e}</p>
          </div> <span class="news-item-date">${n}</span></a>`
            }
            $(".news-main .swiper-wrapper").html(a);
            let e = new Swiper(".news-main-wrap", {
                autoplay: !1,
                loop: !0,
                calculateHeight: !0,
                autoplayDisableOnInteraction: !1,
                swipeToNext: !1,
                swipeToPrev: !1,
                slidesPerView: 2,
                slidesPerGroup: 2
            });
            $(".news-wrap .prev .arrow").click(function () {
                e.swipePrev()
            }), $(".news-wrap .next .arrow").click(function () {
                e.swipeNext()
            })
        }
    })
});